<template>
  <ValidationProvider :name="$attrs.label" :rules="rules">
    <v-autocomplete
      v-model="innerValue"
      v-bind="$attrs"
      slot-scope="{errors, valid}"
      :error-messages="errors"
      :success="valid"
      v-on="$listeners"
      :loading="loading"
      :items="items"
      :search-input.sync="search"
      prepend-icon="search"
      cache-items
      hide-no-data
      hide-details
    ></v-autocomplete>
  </ValidationProvider>
</template>

<script>
import { ValidationProvider } from "vee-validate";

export default {
  name: "VTextFeildWithValidation",
  components: {
    ValidationProvider
  },
  props: {
    rules: {
      type: [Object, String],
      default: ""
    },
    // must be included in props
    value: {
      type: null
    }
  },
  data: () => ({
    loading: false,
    items: [],
    search: null,
    select: null,
    innerValue: ""
  }),
  watch: {
    search(val) {
      // Items have already been loaded
      if (this.items.length > 0) return;

      // Items have already been requested
      if (this.loading) return;

      this.loading = true;

      // Lazily load input items
      fetch("https://gohaulier.com/api/depots/")
        .then(res => res.json())
        .then(res => {
          // console.log(res);
          res.forEach(item => {
            this.items.push(item.postcode);
          });
          console.log(this.items);

          // for (let [id, postcode] of Object.entries(res)) {
          //   console.log(`${id}: ${postcode}`);
          // }

          // const { id, postcode } = res;
          // console.log(id);
          // console.log(postcode);
        })
        .catch(err => {
          console.log(err);
        })
        .finally(() => (this.loading = false));
    },
    // Handles internal model changes.
    innerValue(newVal) {
      this.$emit("input", newVal);
    },
    // Handles external model changes.
    value(newVal) {
      this.innerValue = newVal;
    }
  },
  created() {
    if (this.value) {
      this.innerValue = this.value;
    }
  }
};
</script>

